import React, { useState, useEffect } from 'react';
import Router, { useRouter } from 'next/router';
import Head from 'next/head';

import { getArticleUrl, printDate, getBlogsByCategory } from '../../services/utils';
import NavBar from '../../components/NavBar';
import Footer from '../../components/Footer';

import BlogListContainer from '../../styles/BlogList.style';

export default function Type() {
  const [blogs, setBlogs] = useState([]);
  const { query } = useRouter();
  const { name, id } = query;

  useEffect(() => {
    if (!id) return;
    getBlogsByCategory(id)
      .then((res) => {
        if (res.data && Array.isArray(res.data)) setBlogs(res.data);
        else if (res.data && res.data.data && Array.isArray(res.data.data)) {
          setBlogs(res.data.data.map((b) => ({
            blogId: b.id,
            blogName: b.title,
            date: b.date,
            readTimeMin: b.readTimeMin,
          })));
        }
      })
      .catch(() => {
        Router.push('/', '/', { shallow: false });
      });
  }, [id]);

  const openArticle = (id) => {
    Router.push(`${getArticleUrl(id)}`)
  };

  const list = blogs.map((c) => {
    return (
      <React.Fragment key={c.blogId}>
        <div className="card-wrapper">
          <div onClick={() => openArticle(c.blogId)} className="card">
            <div className="card-body">
              <h5 className="card-title">{c.blogName}</h5>
              <div className="details">{`${printDate(c.date)} - ${c.readTimeMin} mins read`}</div>
              {/* <Link href={url}>
                <a aria-label={c.blogName} className="nav-link">
                  read more
                </a>
              </Link> */}
            </div>
          </div>
        </div>
      </React.Fragment>
    );
  });

  return (
    <>
      <NavBar />
      <Head>
        <title>Dinesh Murali</title>
        <meta name="description" content={`Articles regarding ${name}`} />

        <meta itemProp="name" content="Dinesh Murali" />
        <meta itemProp="description" content={`Articles regarding ${name}`} />
      </Head>
      <BlogListContainer>
        <div className="blog_list__container">
          <div className="container">
            <div className="row">
              <div className="col-12">
                <h1 className="heading-text">
                  {name}
                </h1>
                {name && <p className="lead">Available articles</p>}
              </div>
            </div>
            <div className="d-flex justify-content-start flex-wrap">
              {list}
            </div>
          </div>
        </div>
      </BlogListContainer>
      <Footer />
    </>
  );
}
